<template>
  <div>
    <div class="form_box">
      <el-form size="mini" :model="form" inline>
        <el-form-item>
          <el-select v-model="searchType" placeholder="请选择" style="width: 100px">
            <el-option label="仲裁编号" value="code" />
            <el-option label="买家ID" value="buyerName" />
            <el-option label="卖家ID" value="salerName" />
            <el-option label="订单编号" value="orderCode" />
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-input v-model="searchInput" />
        </el-form-item>
        <!-- <el-form-item label="收货状态">
          <el-select v-model="value" placeholder="请选择" style="width: 100px">
            <el-option label="全部" value="选项1" />
            <el-option label="确认收货" value="选项2" />
            <el-option label="未确认" value="选项3" />
          </el-select>
        </el-form-item> -->
        <el-form-item label="仲裁类型">
          <el-select v-model="form.arbitramentResult" placeholder="请选择" style="width: 100px">
            <el-option label="全部" value="" />
            <el-option label="买家胜诉" :value="0" />
            <el-option label="仲裁驳回" :value="1" />
          </el-select>
        </el-form-item>
        <el-form-item label="发起方">
          <el-select v-model="form.initiatorType" placeholder="请选择" style="width: 100px">
            <el-option label="全部" value="" />
            <el-option label="卖家" :value="1" />
            <el-option label="买家" :value="0" />
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button class="inquire1" type="success" @click="onSearch">查询</el-button>
          <el-button @click="onReset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div>
      <el-table :data="arbitrationList">
        <el-table-column prop="code" label="仲裁编号" align="center"></el-table-column>
        <el-table-column label="发起方" align="center">
          <template slot-scope="scope">
            <!-- 发起发 0-买家 1-卖家 -->
            <span v-if="scope.row.initiatorType === 0">买家</span>
            <span v-else-if="scope.row.initiatorType === 1">卖家</span>
          </template>
        </el-table-column>
        <el-table-column prop="buyerName" label="买家名称" align="center"></el-table-column>center
        <el-table-column prop="salerName" label="卖家名称" align="center"></el-table-column>
        <el-table-column prop="createTime" label="发起时间" align="center"></el-table-column>
        <el-table-column label="收货状态" align="center">
          <!-- 收货状态 0-未收货 1-确认收货 -->
          <template slot-scope="scope">
            <span v-if="scope.row.receiveStatus === 0">未收货</span>
            <span v-else-if="scope.row.receiveStatus === 1">确认收货</span>
          </template>
        </el-table-column>
        <el-table-column label="仲裁种类" align="center">
          <template slot-scope="scope">
            <!-- 仲裁类型 0-仅退款 1-退款退货 2-退换 -->
            <span v-if="scope.row.arbitramentType === 0">仅退款</span>
            <span v-else-if="scope.row.arbitramentType === 1">退款退货</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.arbitramentResult === 0">买家胜诉</span>
            <span v-else-if="scope.row.arbitramentResult === 1">仲裁驳回</span>
            <el-button v-else type="text" size="mini" @click="onDeal(scope)">去处理</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="total" @current-change="handleCurrentChange"></el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ArbitrationOrderList',

  data() {
    return {
      value: '',
      arbitrationList: [], // 仲裁列表
      pageNo: 1, // 分页当前页
      total: 0, // 分页总条数
      form: {}, // 搜索时的表单
      searchType: '', // 搜索类型
      searchInput: '', // 搜索内容
    }
  },

  methods: {
    // 点击去处理
    onDeal(scope) {
      this.$router.push({ path: '/adminOrder/arbitrationOrderList/content', query: { orderId: scope.row.orderId, id: scope.row.id }})
    },
    // 分页
    handleCurrentChange (e) {
      this.pageNo = e
      this.apiGetArbitramentList()
    },
    // 搜索
    onSearch () {
      this.apiGetArbitramentList()
    },
    // 重置
    onReset () {
      this.form = {}
      this.searchType = '' // 搜索类型
      this.searchInput = '' // 搜索内容
    },

    // api接口-----------------------------------------------------------------
    // 仲裁分页查询
    apiGetArbitramentList () {
      let data = {
        pageNo: this.pageNo,
        ...this.form
      }
      // 模糊查询
      data[this.searchType] = this.searchInput
      this.$http.post('/woho/product/arbitrament/query', data)
        .then((res) => {
          this.arbitrationList = res.data.data.list
          this.total = res.data.data.total
        })
    }
  },

  created () {
    this.apiGetArbitramentList()
  }
}
</script>

<style lang="scss" scoped>
  .form_box{
    margin: 15px 0px;
  }
</style>
